@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.switch{display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px dashed #fda94e;padding-top:20px}.prediction-form{margin-top:20px;border:1px dashed #fda94e}.ant-form-item{margin:15px}.ant-form-item-label{text-align:left;margin-right:15px}.ant-form-item-label>label{align-items:flex-start;margin-right:20px;font-weight:bolder}
.container{width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.container .header{display:flex;align-items:center;justify-content:center;width:100%;height:90px}.container .header h1{color:#090909;font-weight:bold;text-transform:uppercase}.container .content{max-width:680px;margin-top:10px;padding:20px}.container .content .result{display:flex;justify-content:center;font-size:20px;margin-top:40px;padding:20px;border:1px dashed #fda94e}.container .content .result .detail{width:350px;display:flex;justify-content:space-between}.container .content .result .detail>span{font-weight:bold;font-size:25px}
